@use 'base/variables' as *;

.alerta  {
    padding: calc($separacion /4);
    color: $blanco;
    width: 100%;
    display: block;
    margin-bottom: 1rem;
    text-align: center;

    &.error {
        background-color: $rojo;
    }
    &.exito {
        background-color: $verde;
    }
}