@use 'variables' as v;
@use 'mixins' as *;

html {
    font-size: 62.5%;
    box-sizing: border-box;
    // height: 100%; // cortaba el linear gradient
    @include tablet {
        height: 100%; // cortaba el linear gradient
    }
}
body {
    display: flex;
    flex-direction: column;
    background: linear-gradient(to bottom, #0F1316, #1D2A32);
    color: v.$blanco;
    font-family: v.$fuente_principal;
    font-size: 1.6rem;
    min-height: 100%;
    @include tablet {
        height: 100%;
    }
}
*, *:before, *:after {
    box-sizing: inherit;
}

.contenedor {
    width: 95%;
    max-width: 1200px;
    margin: 0 auto;
}
a {
    text-decoration: none;
}

img {
    max-width: 100%;
}
